.v-Menu {
    z-index: 1;
    padding: 6px 0;
    min-width: 170px;
    background: none;
    height: 100%;
    font-size: 14px;
    line-height: 12px;
    display: block;
    margin: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    overflow-y: auto;
    outline: none;

}

ul.v-Menu {
    list-style-type: none;
}

.v-MenuItem {
    overflow: hidden;
    display: block;
    outline: none;
    padding:0 15px 0 0;
    min-height: 32px;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    color: #182026;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}

.v-MenuItem.is-active {

    background-color: rgba(167, 182, 194, 0.3);
    z-index: 1;
}

.v-MenuItem.is-active.is-selected {
    background-color: rgba(114, 131, 145, 0.4);

}

.v-MenuItem.is-disabled {
    opacity: 0.6;
}

.v-MenuItem.is-disabled.is-selected,
.v-MenuItem.is-disabled.is-active,
.v-MenuItem.is-disabled.is-active.is-selected {
    background-color: transparent;
}

.v-MenuItem > .icon {

    position: absolute;
    font-size: 19px;
    top:6px;
    left:8px;
    color: #5c7080;
    min-width: 21px;
}

.v-MenuItem > .label {
    padding-left: 36px;
    margin-top:9px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    cursor: default;
}



.v-MenuButton i.chevron {
    text-align: center;
    font-size: 17px;
    margin-right: -10px;
    width: 32px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    opacity: 0.8;
    line-height: 18px;
}

.v-MenuItem i.chevron-right {
    width: 32px;
    height: 16px;
    text-align: center;
    font-size: 15px;
    position:absolute;
    right:2px;
    top:8px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #5c7080;

}

.v-Menu-filter {
    margin: 0 3px 3px 3px;
}

.v-Menu-popover {
    overflow-y: auto;
    overflow-x: hidden;
    outline: none;
}

.v-Menu-Separator {
    height: 1px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #d0d0d0;
    margin: 6px;

}


