.v-Checkbox {
    position: relative;
    min-height: 20px;
    padding-left: 22px;
    text-transform: none;
    width: auto;
    line-height: 16px;
    display: inline-block;
}

.v-Checkbox > .label {
    padding: 0 1px;
    font-size: 14px;
    cursor: default;
}

.v-Checkbox > .indicator {
    background: #f5f8fa;
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #f5f8fa;
    background: -webkit-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: -o-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: linear-gradient(to bottom, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    position: absolute;
    top: 0;
    left: 2px;
    margin: 0;
    border: none;
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.3), inset 0 -1px 0 rgba(16, 22, 26, 0.2);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.3), inset 0 -1px 0 rgba(16, 22, 26, 0.2);
    background-clip: padding-box;
    cursor: default;
    width: 16px;
    height: 16px;
    line-height: 16px;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: FontAwesome, sans-serif;
    z-index: 0;
}

.v-Checkbox.is-focused > .indicator {
    -webkit-box-shadow: inset 0 0 0 1px rgba(32, 86, 156, 0.6), inset 0 -1px 0 rgba(29, 105, 175, 0.3);
            box-shadow: inset 0 0 0 1px rgba(32, 86, 156, 0.6), inset 0 -1px 0 rgba(29, 105, 175, 0.3);
}

.v-Checkbox.is-hover:not(.is-disabled) > .indicator {
    background: #ebf1f5;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #ebf1f5;
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;
    background: -o-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;

}

.v-Checkbox > .indicator:before {
    position: relative;
    content: "";
}

.v-Checkbox > input:checked + .indicator {
    background: #137cbd;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #137cbd;
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    background: -o-linear-gradient(top, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.4), inset 0 -1px 0 rgba(16, 22, 26, 0.2);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.4), inset 0 -1px 0 rgba(16, 22, 26, 0.2);
    color: #ffffff;
}

.v-Checkbox > input:checked + .indicator:before {
    content: "\f00c";
}

.v-Checkbox > input {
    position: absolute;
    top: 0;
    left: 0;
    cursor: default;
    width: 15px;
    height: 16px;
    opacity: 0;
    z-index: 1;
}

.v-Checkbox.is-disabled,
.v-Checkbox.is-disabled > .indicator,
.v-Checkbox.is-disabled > .label {
    opacity: 0.75;
    cursor: default;
}

.v-Checkbox.is-disabled > input {
    cursor: default;
}

.v-Switch {
    padding-left: 36px;
    display: inline-block;
    position: relative;
    margin-bottom: 10px;
    cursor: default;
    min-height: 22px;
    text-transform: none;
    line-height: 15px;
    width: auto;
}

.v-Switch > .indicator {
    border: none;
    border-radius: 28px;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: rgba(167, 182, 194, 0.5);
    width: 28px;
    height: 16px;
    -webkit-transition: background 100ms cubic-bezier(0.4, 1, 0.75, 0.9);
    -o-transition: background 100ms cubic-bezier(0.4, 1, 0.75, 0.9);
    transition: background 100ms cubic-bezier(0.4, 1, 0.75, 0.9);

    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;

    position: absolute;
    top: 0;
    left: 0;
    margin: 0;

    background-clip: padding-box;
    cursor: default;
    line-height: 16px;

}

.v-Switch > .indicator::before {

    margin: 0;
    display: block;
    top: 2px;
    left: 2px;
    border-radius: 16px;
    -webkit-box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
            box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
    background: #ffffff;
    background-clip: padding-box;
    width: 12px;
    height: 12px;
    content: "";
    -webkit-transition: left 100ms cubic-bezier(0.4, 1, 0.75, 0.9);
    -o-transition: left 100ms cubic-bezier(0.4, 1, 0.75, 0.9);
    transition: left 100ms cubic-bezier(0.4, 1, 0.75, 0.9);
    position: relative;
}

.v-Switch.is-checked > .indicator {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #137cbd;
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    background: -o-linear-gradient(top, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #137cbd;
    -webkit-box-shadow: none;
            box-shadow: none;
    width: 28px;
    height: 16px;
}

.v-Switch.is-checked > .indicator:before {
    left: 14px;
    -webkit-box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
            box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
}

.v-Switch > label {
    margin: 0;
    font-size: 14px;
    cursor: default;
}


