.v-Select {
    position: relative;
    width: 150px;
}

.v-Select > .v-Select-input {
    font-size: 13px;
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #f5f8fa;
    background: -webkit-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: -o-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: linear-gradient(to bottom, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
    color: #182026;
    -webkit-appearance: none;
    -moz-appearance: none;
    border-radius: 3px;
    height: 30px;
    padding: 0 25px 0 10px;
    width: 100%;
    vertical-align: top;
    font-weight: 400;
    display: inline-block;
    border: none;
    cursor: pointer;
    outline: none;
}

.v-Select > select.v-Select-input::-ms-expand {
    display: none;
}

.v-Select > .v-Select-input:hover {
    background: #ebf1f5;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #ebf1f5;
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;
    background: -o-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #ebf1f5;
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
    background-clip: padding-box;
}

.v-Select > select:focus::-ms-value {
    background-color: white;
    color: #000;
}

.v-Select.is-disabled > .v-Select-input:hover {
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(rgba(255, 255, 255, 0))) left no-repeat, center no-repeat #f5f8fa;
    background: -webkit-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: -o-linear-gradient(top, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    background: linear-gradient(to bottom, #ffffff, rgba(255, 255, 255, 0)) left no-repeat, center no-repeat #f5f8fa;
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
}

.v-Select > .v-Select-input.is-active {
    -webkit-box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 1px 2px rgba(16, 22, 26, 0.2);
            box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 1px 2px rgba(16, 22, 26, 0.2);
    background-color: #d8e1e8;
    background-image: none;
}

.v-Select::after {
    position: absolute;
    top: 0;
    right: 12px;
    line-height: 29px;
    color: #5c7080;
    content: "\f078";
    pointer-events: none;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    font-family: 'FontAwesome';

}

.v-Select.is-disabled .v-Select-input,
.v-Select.is-disabled {
    opacity: 0.92;
    cursor: default;
}


