.v-Pager .v-Button {
    width:28px;
    height:28px;
    text-align:center;
    padding:0;
    border-left-width: 0;
}

.v-Pager .v-Button.first {
    border-left-width: 1px;
}

.v-Pager .v-Button .label {
    font-size:12px;
}

.v-Pager .v-Button.primary.selected {
    border-left-width: 1px;
}





