
.v-ProgressiveList {
    position: relative;
}

.v-ProgressiveList > .v-scrollview {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.v-ProgressiveList > .v-scrollview > div {
    position: relative;
}

.v-ProgressiveList > .v-scrollview > div > * {
    position: absolute !important;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}


