.v-MessageBox .v-WindowPanel-content .icon {

    font-size:96px;
    position:absolute;
    top:45px;
    left:20px;
    
}

.v-MessageBox .v-WindowPanel-content .icon.fa-exclamation-triangle {
    color: #f5d35a;
}

.v-MessageBox .v-WindowPanel-content .icon.fa-question-circle {
    color: #769fb6;

}

.v-MessageBox .v-WindowPanel-content .message {
    position:absolute;
    left:140px;
    top:45px;
    right:20px;
    bottom:70px;
    font-size:16px;
    color:#4f4f4f;
}

.v-MessageBox .v-WindowPanel-content .v-Text {
    margin-top:8px;
}

.v-MessageBox .v-WindowPanel-content .v-Button  {
    position:absolute;
    width:110px;
    height: 35px;
}

.v-MessageBox .v-WindowPanel-content .v-Button.confirm {
    right:20px;
    bottom:20px;
}

.v-MessageBox .v-WindowPanel-content .v-Button.cancel {
    right:138px;
    bottom:20px;
}




