.v-Button {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;

    padding: 3px 12px 4px 8px;

    white-space: nowrap;
    text-align: center;

    border: 1px solid transparent;

    font-size: 14px;

    min-height: 21px;
    line-height: 100%;
    border-radius: 2px;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    cursor: pointer;

    background: none;
    color: #333333;

    height: 32px;
    min-width:16px;
    position: relative;

}

.v-Button.is-disabled {
    opacity: 0.6;
    cursor: default !important;
}

.v-Button.default {
    background-color: #fff;
    border: 1px solid #ccc;
    color: #333;
    -webkit-transition: background-color 0.1s, color 0.1s, opacity 0.1s;
    -o-transition: background-color 0.1s, color 0.1s, opacity 0.1s;
    transition: background-color 0.1s, color 0.1s, opacity 0.1s;
}

.v-Button.default.is-hover {
    background-color: #f1f1f1;
}

.v-Button.default.is-disabled.is-hover {
    background-color: #fff;
}

.v-Button.default.is-active {
    background-color: #eee;
    -webkit-box-shadow: inset 0 3px 5px rgba(50, 50, 50, .125);
    box-shadow: inset 0 3px 5px rgba(50, 50, 50, .125);
}

.v-Button > .label {
    padding: 0;

}

.v-Button--hasIcon > .label {
    padding-left: 22px;

}

.v-Button > .icon {
    font-size: 17px;
    margin: 0;
    opacity: 0.92;
    position: absolute;
    left: 7px;
    top: 6px;

}

.v-Button--hasIcon > .icon {
    display: inline-block;
}

.v-Button.v-Button--iconOnly {
    padding:0;
}

.v-Button.v-Button--iconOnly > .icon {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    left:0;
    top:0;
    bottom:2px;
    right:0;
    margin:0;
    padding:0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}



