.v-Toolbar {
    height: 44px;
    background-color: #394b59;
    border-bottom: 1px solid #28343e;
    color: #f5f8fa;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}

.v-Toolbar-section {
    position: absolute;
    top: 50%;
    margin-top: -16px;
    height: 32px;
    line-height: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.v-Toolbar-section.v-Toolbar-section--left {
    left: 8px;

}

.v-Toolbar-section.v-Toolbar-section--right {
    right: 8px;
}

.v-Toolbar-section > * {
    display: inline-block;
    position: relative !important;

}

.v-Toolbar-section > .v-Button.default {
    border: 1px solid transparent;
    border-radius: 3px;
    height: 32px;
    background-color: transparent;
    color: #fff;
    -webkit-box-shadow: none;
            box-shadow: none;
    text-shadow: none;
    margin: 0;
    line-height: 16px;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.v-Toolbar-section > .v-Button.default.is-hover {
    background-color: rgba(138, 155, 168, 0.15);
}

.v-Toolbar-section > .v-Button.default.is-active {
    background-color: rgba(138, 155, 168, 0.3);
    color: #f5f8fa;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.v-Toolbar-section > .hidden {
    display: none !important;
}

.v-Toolbar-section > .v-MenuButton i.chevron {
    color: #fff;
}

.v-Toolbar-section > .v-Text {
    margin: 0 3px 0 3px;
    height: 32px;
    border-color: #0d3b5f;
    border-radius: 2px;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    vertical-align: top;
}

.v-Toolbar-section > .v-Label {
    color: #fff;
    margin: 0 3px 0 3px;
}

.v-Toolbar-section > .v-Text .v-Text-input {
    background-color: #e7f0f5;
    padding-top: 0;
    padding-bottom: 0;
}

.v-Toolbar-section > .v-Text.is-focused {
    border-color: #004669;
}

.v-Toolbar-section > .v-Text.is-focused .v-Text-input {
    background-color: #fff;
    border-color: transparent;
}

.v-Toolbar-section > .v-Slider {
    margin: 12px 2px 0 3px;
    position: relative;
    background-color: #fafafa;
}

.v-Toolbar-section > .v-Slider .v-Slider-range {
    background-color: #feffcf;
}

.v-Toolbar-divider {
    display: inline;
    height: 32px;
    width: 1px;
    padding: 3px 0 6px 0;
    margin: 0 5px 0 5px;
    border-left: 1px solid #0d3b5f;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}


